//base
@import "base/normalise";
@import "base/fonts";
@import "base/vars";

html {
    position: relative; }

body {
  font-family: 'Fira Sans', sans-serif;
  color: #fff;
  background-image: url(../img/bg.png);
  background-repeat: no-repeat;
  background-size: 100vw auto;
  background-position: 100% 0%;
  background-repeat: no-repeat;



  .container {
    max-width: 1460px;
    margin: 0 auto; }
  .vert-line {
    position: absolute;
    width: get-vw(3px);
    height: get-vw(150px);
    top: get-vw(200px);
    left: get-vw(235px);
    background: linear-gradient(180deg, #85daec , #556ccd); }
  .horiz-line {
    position: absolute;
    width: get-vw(150px);
    height: get-vw(3px);
    top: get-vw(670px);
    left: get-vw(235px);
    background: linear-gradient(90deg, #556ccd, #85daec); }
  .rect1 {
      width: get-vw(500px);
      height:  get-vw(800px);
      position: absolute;
      z-index: 5;
      right: get-vw(240px);
      top: 0;
      background: linear-gradient( 30deg, rgb(71,94,186) 0%, rgb(141,235,255) 100%);
      border-bottom-right-radius: 5px;
      border-bottom-left-radius: 5px; }

  .rect2 {
      width: get-vw(500px);
      height:  get-vw(820px);
      position: absolute;
      z-index: 4;
      right: get-vw(220px);
      top: 0;
      background: linear-gradient(45deg,#5a78d0 5%,#85daec 96%);
      opacity: 0.6;
      border-bottom-right-radius: 5px;
      border-bottom-left-radius: 5px; }

  h1 {
    font-weight: 600;
    font-size: get-vw(60px);
    padding-top: 23%;
    width: 85%;
    position: relative;
    z-index: 7; }


  h2 {
    font-weight: 300;
    font-size: get-vw(48px);
    position: relative; }

  p {
    font-weight: 500;
    font-size: get-vw(16px);
    text-transform: uppercase;
    margin: 0; }


  .bottom-content {
    padding-top: 7%;
    display: flex;
    justify-content: space-between; }
  .time {
    width: 30%;
    padding-top: 20px;
    position: relative;
    z-index: 9;
    &-wrapper {
      display: flex;
      font-weight: 900;
      font-size: get-vw(60px);
      div:not(:first-child) {
        padding-left: 10px; } } } }

@import "media/media";




