@import url("https://fonts.googleapis.com/css?family=Fira+Sans:300,500,600,900&subset=cyrillic-ext");
html {
  font-family: sans-serif;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  margin: 0; }

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section,
summary {
  display: block; }

audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline; }

audio:not([controls]) {
  display: none;
  height: 0; }

[hidden],
template {
  display: none; }

a {
  background-color: transparent; }

a:active,
a:hover {
  outline: 0; }

abbr[title] {
  border-bottom: 1px dotted; }

b,
strong {
  font-weight: bold; }

dfn {
  font-style: italic; }

h1 {
  font-size: 2em;
  margin: 0.67em 0; }

mark {
  background: #ff0;
  color: #000; }

small {
  font-size: 80%; }

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sup {
  top: -0.5em; }

sub {
  bottom: -0.25em; }

img {
  border: 0; }

svg:not(:root) {
  overflow: hidden; }

figure {
  margin: 1em 40px; }

hr {
  box-sizing: border-box;
  height: 0; }

pre {
  overflow: auto; }

code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em; }

button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0; }

button {
  overflow: visible; }

button,
select {
  text-transform: none; }

button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer; }

button[disabled],
html input[disabled] {
  cursor: default; }

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0; }

input {
  line-height: normal; }

input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box;
  padding: 0; }

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  height: auto; }

input[type="search"] {
  -webkit-appearance: textfield;
  box-sizing: border-box; }

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none; }

fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em; }

legend {
  border: 0;
  padding: 0; }

textarea {
  overflow: auto; }

optgroup {
  font-weight: bold; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

td,
th {
  padding: 0; }

html {
  position: relative; }

body {
  font-family: 'Fira Sans', sans-serif;
  color: #fff;
  background-image: url(../img/bg.png);
  background-repeat: no-repeat;
  background-size: 100vw auto;
  background-position: 100% 0%;
  background-repeat: no-repeat; }
  body .container {
    max-width: 1460px;
    margin: 0 auto; }
  body .vert-line {
    position: absolute;
    width: 0.15625vw;
    height: 7.8125vw;
    top: 10.41667vw;
    left: 12.23958vw;
    background: linear-gradient(180deg, #85daec, #556ccd); }
  body .horiz-line {
    position: absolute;
    width: 7.8125vw;
    height: 0.15625vw;
    top: 34.89583vw;
    left: 12.23958vw;
    background: linear-gradient(90deg, #556ccd, #85daec); }
  body .rect1 {
    width: 26.04167vw;
    height: 41.66667vw;
    position: absolute;
    z-index: 5;
    right: 12.5vw;
    top: 0;
    background: linear-gradient(30deg, #475eba 0%, #8debff 100%);
    border-bottom-right-radius: 5px;
    border-bottom-left-radius: 5px; }
  body .rect2 {
    width: 26.04167vw;
    height: 42.70833vw;
    position: absolute;
    z-index: 4;
    right: 11.45833vw;
    top: 0;
    background: linear-gradient(45deg, #5a78d0 5%, #85daec 96%);
    opacity: 0.6;
    border-bottom-right-radius: 5px;
    border-bottom-left-radius: 5px; }
  body h1 {
    font-weight: 600;
    font-size: 3.125vw;
    padding-top: 23%;
    width: 85%;
    position: relative;
    z-index: 7; }
  body h2 {
    font-weight: 300;
    font-size: 2.5vw;
    position: relative; }
  body p {
    font-weight: 500;
    font-size: 0.83333vw;
    text-transform: uppercase;
    margin: 0; }
  body .bottom-content {
    padding-top: 7%;
    display: flex;
    justify-content: space-between; }
  body .time {
    width: 30%;
    padding-top: 20px;
    position: relative;
    z-index: 9; }
    body .time-wrapper {
      display: flex;
      font-weight: 900;
      font-size: 3.125vw; }
      body .time-wrapper div:not(:first-child) {
        padding-left: 10px; }

@media only screen and (max-width: 756px) {
  body {
    background-image: url(../img/bg.png);
    background-repeat: no-repeat;
    background-size: auto 100vh;
    background-position: 0 0;
    background-repeat: no-repeat; }
    body h1 {
      text-align: center;
      font-size: 7.6875vw;
      padding-top: 50%;
      width: 100%; }
      body h1:before {
        left: 6.64583vw;
        top: 50.25vw;
        height: 20%; }
    body h2 {
      text-align: center;
      font-size: 5.5vw; }
      body h2:before {
        width: 13%; }
    body p {
      font-size: 5.83333vw; }
    body .bottom-content {
      justify-content: center;
      flex-direction: column; }
    body .time {
      width: 70%;
      margin: 0 auto; }
    body .time-wrapper {
      font-size: 10.125vw; }
    body .rect1, body .rect2 {
      display: none; }
    body .vert-line {
      display: none; } }
