@media only screen and (max-width : 756px) {
  body {
    background-image: url(../img/bg.png);
    background-repeat: no-repeat;
    background-size: auto 100vh;
    background-position: 0 0;
    background-repeat: no-repeat;
    h1 {
      text-align: center;
      font-size:  7.6875vw;
      padding-top: 50%;
      width: 100%;
      &:before {
        left:  6.64583vw;
        top: 50.25vw;
        height: 20%; } }

    h2 {
      text-align: center;
      font-size: 5.5vw;
      &:before {
        width: 13%; } }
    p {
      font-size: 5.83333vw; }
    .bottom-content {
      justify-content: center;
      flex-direction: column; }
    .time {
      width: 70%;
      margin: 0 auto; }
    .time-wrapper {
      font-size: 10.125vw; }
    .rect1, .rect2 {
      display: none; }
    .vert-line {
        display: none; } } }
